* {
  box-sizing: border-box;
  margin: 0; }

body {
  background-color: white;
  font-family: 'Comfortaa'; }

.container {
  background-color: lightblue;
  width: 700px;
  height: 520px;
  margin: auto;
  margin-top: 100px; }

header {
  background-color: #f8f8f8;
  min-height: 100px;
  padding-top: 40px;
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 10px;
  align-items: center;
  box-shadow: 0 0px 1px rgba(0, 0, 0, 0.16); }
  header .header-x-box {
    height: 50px;
    width: 150px;
    justify-self: end;
    background-color: white;
    cursor: pointer; }
    header .header-x-box #cross-small {
      height: 80%;
      width: 80%;
      position: relative;
      top: 0;
      left: 0;
      transition: all 1s;
      z-index: 20000; }
      header .header-x-box #cross-small .cross-small-line {
        position: absolute;
        top: 21px;
        left: 54px;
        width: 40px;
        height: 7px;
        background-color: #545454; }
      header .header-x-box #cross-small .small-line-1 {
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg); }
      header .header-x-box #cross-small .small-line-2 {
        -webkit-transform: rotate(135deg);
                transform: rotate(135deg); }
  header .header-o-box {
    height: 50px;
    width: 150px;
    background-color: white;
    cursor: pointer; }
    header .header-o-box #zero-small {
      height: 70%;
      width: 70%;
      position: relative;
      top: 0;
      left: 0;
      transition: all 1s; }
      header .header-o-box #zero-small div {
        position: absolute;
        width: 40px;
        height: 40px;
        top: 5px;
        left: 54px;
        border-radius: 50%;
        border: 7px solid #f2ebd3; }
  header .header-game-text {
    color: grey;
    font-size: 15px;
    grid-column: 1/4;
    text-align: center; }

#game-board {
  display: grid;
  position: relative;
  width: 280px;
  height: 280px;
  margin: 50px auto;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr; }
  #game-board .item {
    font-size: 50px;
    font-weight: lighter;
    border-collapse: collapse;
    border-right: 7px solid rgba(0, 0, 0, 0.3);
    border-bottom: 7px solid rgba(0, 0, 0, 0.3);
    display: grid;
    align-items: center;
    justify-items: center; }
  #game-board .item-3, #game-board .item-6, #game-board .item-9 {
    border-right: none; }
  #game-board .item-7, #game-board .item-8, #game-board .item-9 {
    border-bottom: none; }

#cross {
  height: 80%;
  width: 80%;
  position: relative; }
  #cross .cross-line {
    position: absolute;
    top: 32px;
    left: 6px;
    width: 60px;
    height: 7px;
    background-color: #545454; }
  #cross .line-1 {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); }
  #cross .line-2 {
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg); }

#zero {
  height: 70%;
  width: 70%; }
  #zero div {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: 7px solid #f2ebd3; }

#game-results {
  width: 280px;
  height: 100px;
  top: 390px;
  left: 46.5%;
  line-height: 280px;
  position: absolute;
  z-index: 100;
  font-size: 50px;
  font-weight: bold;
  opacity: 0;
  color: #545454; }

.reset-game {
  text-align: center;
  height: 60px;
  line-height: 60px;
  background-color: #f8f8f8;
  color: grey;
  cursor: pointer;
  font-weight: bold;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.16); }

#highlight {
  position: absolute;
  transition: all 1s;
  z-index: 1000; }

@keyframes appear {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes appear {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes blur {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@-webkit-keyframes blur {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@keyframes win-left-right {
  from {
    width: 0; }
  to {
    width: 250px; } }

@-webkit-keyframes win-left-right {
  from {
    width: 0; }
  to {
    width: 250px; } }

@keyframes win-top-bottom {
  from {
    height: 0; }
  to {
    height: 250px; } }

@-webkit-keyframes win-top-bottom {
  from {
    height: 0; }
  to {
    height: 250px; } }

@keyframes win-diag-159 {
  0% {
    width: 0px; }
  20% {
    width: 300px;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); } }

@-webkit-keyframes win-diag-159 {
  0% {
    width: 0px; }
  20% {
    width: 300px;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg); } }

@keyframes win-diag-357 {
  0% {
    width: 0px; }
  20% {
    width: 300px;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg); } }

@-webkit-keyframes win-diag-357 {
  0% {
    width: 0px; }
  20% {
    width: 300px;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg); } }

